//
// Typography
// --------------------------------------------------


// Headings
// -------------------------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	font-family: @font-family-merriweather;
	font-weight: @headings-font-weight;
	line-height: @headings-line-height;
	color: @headings-color;

	small,
	.small {
		font-weight: normal;
		line-height: 1;
		color: @headings-small-color;
	}
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	margin-top: 0px;
	small,
	.small {
		font-size: 65%;
	}
}
h4, .h4,
h5, .h5,
h6, .h6 {
	small,
	.small {
		font-size: 75%;
	}
}

h1, .h1 { font-size: @font-size-h1; }
h2, .h2 { font-size: @font-size-h2; }
h3, .h3 { font-size: @font-size-h3; }
h4, .h4 { font-size: @font-size-h4; }
h5, .h5 { font-size: @font-size-h5; }
h6, .h6 {
	font-size: @font-size-h6;
	text-transform: uppercase;
	font-family: @font-family-sans-serif;
}


// Body text
// -------------------------

a, p {
	font-family: @font-family-sans-serif;
	margin: 0 0 (@line-height-computed / 2);
	font-size: 15px;
	@media (min-width: @screen-sm){ font-size: 17px; }
}

.lead {
	font-family: @font-family-merriweather;
	margin-bottom: @line-height-computed;
	font-size: @font-size-h3;
	font-weight: 400;
	line-height: 1.7em;

	@media (min-width: @screen-sm-min) {
		font-size: (@font-size-base * 1.5);
	}
}

.text-italic{
	font-style: italic;
}

// Emphasis & misc
// -------------------------

// Ex: (12px small font / 14px base font) * 100% = about 85%
small,
.small {
	font-size: floor((100% * @font-size-small / @font-size-base));
}

mark,
.mark {
	background-color: @state-warning-bg;
	padding: .2em;
}

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-justify        { text-align: justify; }
.text-nowrap         { white-space: nowrap; }

// Transformation
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }

// Contextual colors
.text-muted {
	color: @text-muted;
}
.text-primary {
	.text-emphasis-variant(@brand-primary);
}
.text-success {
	.text-emphasis-variant(@state-success-text);
}
.text-info {
	.text-emphasis-variant(@state-info-text);
}
.text-warning {
	.text-emphasis-variant(@state-warning-text);
}
.text-danger {
	.text-emphasis-variant(@state-danger-text);
}

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-primary {
	// Given the contrast here, this is the only class to have its color inverted
	// automatically.
	color: #fff;
	.bg-variant(@brand-primary);
}
.bg-success {
	.bg-variant(@state-success-bg);
}
.bg-info {
	.bg-variant(@state-info-bg);
}
.bg-warning {
	.bg-variant(@state-warning-bg);
}
.bg-danger {
	.bg-variant(@state-danger-bg);
}


// Page header
// -------------------------

.page-header {
	padding-bottom: ((@line-height-computed / 2) - 1);
	margin: (@line-height-computed * 2) 0 @line-height-computed;
	border-bottom: 1px solid @page-header-border-color;
}


// Lists
// -------------------------

// Unordered and Ordered lists
ul,
ol {
	margin-top: 0;
	margin-bottom: (@line-height-computed / 2);
	ul,
	ol {
		margin-bottom: 0;
	}
}

// List options

// Unstyled keeps list items block level, just removes default browser padding and list-style
.list-unstyled {
	padding-left: 0;
	list-style: none;
}

// Inline turns list items into inline-block
.list-inline {
	.list-unstyled();
	margin-left: -5px;

	> li {
		display: inline-block;
		padding-left: 5px;
		padding-right: 5px;
	}
}

li{
	margin-bottom: (@margin-base/2);
}
// Description Lists
dl {
	margin-top: 0; // Remove browser default
	margin-bottom: @line-height-computed;
}
dt,
dd {
	line-height: @line-height-base;
}
dt {
	font-weight: bold;
}
.fw-bold{
	font-weight: 700;
}
dd {
	margin-left: 0; // Undo browser default
}

// Horizontal description lists
//
// Defaults to being stacked without any of the below styles applied, until the
// grid breakpoint is reached (default of ~768px).

.dl-horizontal {
	dd {
		&:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present
	}

	@media (min-width: @dl-horizontal-breakpoint) {
		dt {
			float: left;
			width: (@dl-horizontal-offset - 20);
			clear: left;
			text-align: right;
			.text-overflow();
		}
		dd {
			margin-left: @dl-horizontal-offset;
		}
	}
}


// Misc
// -------------------------

// Abbreviations and acronyms
abbr[title],
// Add data-* attribute to help out our tooltip plugin, per https://github.com/twbs/bootstrap/issues/5257
abbr[data-original-title] {
	cursor: help;
	border-bottom: 1px dotted @abbr-border-color;
}
.initialism {
	font-size: 90%;
	.text-uppercase();
}

// Blockquotes
blockquote {
	padding: (@line-height-computed / 2) @line-height-computed;
	margin: 0 0 @line-height-computed;
	font-size: @blockquote-font-size;
	border-left: 5px solid @blockquote-border-color;

	p,
	ul,
	ol {
		&:last-child {
			margin-bottom: 0;
		}
	}

	// Note: Deprecated small and .small as of v3.1.0
	// Context: https://github.com/twbs/bootstrap/issues/11660
	footer,
	small,
	.small {
		display: block;
		font-size: 80%; // back to default font-size
		line-height: @line-height-base;
		color: @blockquote-small-color;

		&:before {
			content: '\2014 \00A0'; // em dash, nbsp
		}
	}
}

// Opposite alignment of blockquote
//
// Heads up: `blockquote.pull-right` has been deprecated as of v3.1.0.
.blockquote-reverse,
blockquote.pull-right {
	padding-right: 15px;
	padding-left: 0;
	border-right: 5px solid @blockquote-border-color;
	border-left: 0;
	text-align: right;

	// Account for citation
	footer,
	small,
	.small {
		&:before { content: ''; }
		&:after {
			content: '\00A0 \2014'; // nbsp, em dash
		}
	}
}

// Addresses
address {
	margin-bottom: @line-height-computed;
	font-style: normal;
	line-height: @line-height-base;
}

//font size
.fz--small, .fz--small p{
	font-size: 13px;
	@media (min-width: @screen-sm){ font-size: 15px!important; }
}

.fz-medium{
	font-size: 15px;
	@media (min-width: @screen-sm){ font-size: 17px!important; }
}

.fz--large{
	font-size: 17px;
	@media (min-width: @screen-sm){ font-size: 19px; }
}

//font size
.text--xsmall{
	font-size: 11px;
	@media (min-width: @screen-sm){ font-size: 13px; }
}

.text--small, .text--small p{
	font-size: 13px;
	@media (min-width: @screen-sm){ font-size: 15px!important; }
}

.text--medium{
	font-size: 15px;
	@media (min-width: @screen-sm){ font-size: 17px!important; }
}

.text--large{
	font-size: 17px;
	@media (min-width: @screen-sm){ font-size: 19px; }
}

.text--xlarge{
	font-size: 19px;
	@media (min-width: @screen-sm){ font-size: 21px; }
}

//Links

a {
	cursor: pointer;
	color: @primary;
}

.link--primary{
	&:hover{color: @primary--medium;}
	&.active &:focus, &:visited { color: @primary--dark;}
}

.text-block a{
	display: block;
}
.block--xs a{
	@media (max-width: @screen-sm){ display: block; }
}

p a.link--primary{ text-decoration: underline; }

.bg-gray-base {
	& p{
		color: @light;
	}
	.link{
	 color: @secondary;
	 text-decoration: underline;
	}
}

//Text bold

.text-resume{
	p:nth-child(2n+1){
		font-weight: 700;
		margin-bottom: 0px;
	}
}

.subtitle{
	font-weight: 700;
	font-size: 19px;
	color: @gray;
}

.subtitle--large{
	font-weight: 700;
	font-size: 30px;
}

//Contenedores
.content-resultados{
	a{
		color: @primary;
		display: block;
		span{ color: @secondary!important;}
	}
}

//list style
.list-disc *{
	margin-left:(@margin-base-horizontal*2);
	list-style:disc outside none;
}

.list-decimal *{ list-style:decimal inside none; }

//Line height

.line-height--1{ line-height: 1; }